.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.row {
  display: flex;
  justify-content: center;
  cursor: pointer;
  position: relative;
  top: 20px;
  /* margin-top: 60px; */
}

.row button {
  background-color: transparent;
  outline: none;
  border: none;
  font-size: calc(15px + 9 * ((100vw - 320px) / (1920 - 320)));
  font-weight: 600;
  color: rgb(155, 155, 155);
  z-index: 4;
  cursor: pointer;
}

.row button.active {
  color: black
}

.diagram {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.diagram p {
  position: absolute;
}

.admin-button {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

@media (max-width: 990px) {
  .row button {
    margin-bottom: 10px;
  }
  
}

.not_understand, .understand {
  color: white;
  // font-size: 18px;
  font-size: calc(13px + 5 * ((100vw - 320px) / (1920 - 320)));
  padding: 15px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
}

.not_understand {
  background-color: red;
}

.understand {
  background-color: green;
  margin-right: 50px;
}

@media (max-width: 1200px) {
  .understand {
    margin-right: 25px;
  }
}

.not_understand:hover {
  background-color: rgb(165, 0, 0);
}

.understand:hover {
  background-color: rgb(0, 85, 0);
}

.label > p {
  color: red;  
  font-size: calc(13px + 5 * ((100vw - 320px) / (1920 - 320)));
  font-weight: 600;
}

.info__row {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

@media (max-width: 767px) {
  .info__row {
    flex-direction: column;
    margin-bottom: 20px;
  }

  .label {
    height: 120px;
  }
}

@media (max-width: 480px) {
  .diagram {
    display: none;
  }

  .label {
    height: auto;
  }
}


.label {
  display: flex;
  align-items: center;
}